











































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import service from '@/service/index'
import * as api from '@/api'

interface Add {
  form: Object
  formLabelWidth: String
  dialogFormVisible: Boolean
}

@Component
export default class AddAdmin extends Vue implements Add{
  @Prop() private visible!: Boolean

  form = {
    username: '',
    password: '',
    nickname: ''
  }
  formLabelWidth = '120px'
  dialogFormVisible = this.visible

  options = [{
    value: '北京',
    label: '北京',
    children: [{
      value: '朝阳区',
      label: '朝阳区',
    }, {
      value: '海淀区',
      label: '海淀区',
    }, {
      value: '东城区',
      label: '东城区',
    }, {
      value: '西城区',
      label: '西城区',
    }, {
      value: '其他',
      label: '其他',
    }]
  }, {
    value: '其他地区',
    label: '其他地区',
  }]

  @Watch('visible')
  onVisibleChange(val: Boolean, oldVal: Boolean) {
    this.dialogFormVisible = val
    for (let key in this.form) {
      this.form[key] = ''
    }
  }

  @Watch('dialogFormVisible')
  onDialogFormVisibleChange(val: Boolean, oldVal: Boolean) {
    if (!val) {
      this.$emit('getChildData', val)
    }
  }

  public addAdmin() {
    // console.log(this.form)
    api.AddDropdown(this.form).then((res) => {
      // console.log(res)
      this.dialogFormVisible = false
      this.$message({
        message: res.message,
        type: res.result
      })
      this.$emit('getAdmin')
    })
    // service.postAdminList(this.form).then(res => {
    //   this.dialogFormVisible = false
    //   let { code, msg } = res.data
    //   if (code == 0) {
    //     this.$message({
    //       message: msg,
    //       type: 'success'
    //     })
    //   }
    //   this.$emit('getAdmin')
    // })
  }
}
